nav {
    display: flex;
    justify-content: flex-end;
    min-height: 28%;
    background: url('../../img/slbBanner2.jpg');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
}

.nav-links {
    width: 50%;
    display: flex;
    justify-content: space-evenly;
    align-items: end;
    list-style: none;
    padding-bottom: 8em;
}

.active-link {
    background-color: #C00;
    color: #FFF;
}